:translate(0, -50%)}.add-location-input .button.enter{opacity:0;-webkit-transform:translate(50%, -50%);transform:translate(50%, -50%)}.add-location-input .button.enter-active{opacity:1;-webkit-transform:translate(0, -50%);transform:translate(0, -50%)}.add-location-input .button.exit{opacity:1;-webkit-transform:translate(0, -50%);transform:translate(0, -50%)}.add-location-input .button.exit-active{opacity:0;-webkit-transform:translate(50%, -50%);transform:translate(50%, -50%)}@media (max-width: 1024px){.add-location-input .button{width:38px;height:38px}}.add-location-input .button svg{font-size:24px;color:white}

.weather-settings-screen{position:absolute;top:0;left:0;width:100%;height:100%;z-index:200;background-color:#1a1a1a;display:flex;flex-direction:column;overflow:hidden}.weather-settings-screen.enter{opacity:0}.weather-settings-screen.enter-active{opacity:1;transition:0.15s}.weather-settings-screen.exit{opacity:1}.weather-settings-screen.exit-active{opacity:0;transition:0.15s}.weather-settings-screen .header{flex-shrink:0}.weather-settings-screen .header .header-container{padding-top:15px;padding-bottom:15px;display:flex}.weather-settings-screen .header .close-button{align-self:flex-start;cursor:pointer;color:white;font-size:20px;display:flex;align-items:center;text-decoration:none}.weather-settings-screen .header .close-button svg{margin-right:10px;font-size:30px}.weather-settings-screen .weather-settings-main{overflow:overlay;flex:1 1;position:relative}.weather-settings-screen .weather-settings-main::-webkit-scrollbar{width:10px}@media (max-width: 1024px){.weather-settings-screen .weather-settings-main::-webkit-scrollbar{width:8px}}.weather-settings-screen .weather-settings-main::-webkit-scrollbar-track{background-color:transparent}.weather-settings-screen .weather-settings-main::-webkit-scrollbar-thumb{background:rgba(255,255,255,0.2);border-radius:4px}.weather-settings-screen .weather-settings-main .locations-list{overflow:hidden}

*{padding:0;margin:0;border:0;outline:0;box-sizing:border-box;-webkit-tap-highlight-color:rgba(0,0,0,0)}html,body,#root{height:100%}body{min-width:320px;font-family:"Roboto",sans-serif;background-color:#2c2933}#root{display:flex;flex-direction:column;overflow:hidden}.container{max-width:1230px;padding-left:20px;padding-right:20px;margin:0 auto}@media (max-width: 1024px){.container{padding-left:15px;padding-right:15px}}.screen{flex:1 1;padding-bottom:64px;height:100%;width:100%;position:absolute;top:0;left:0;z-index:1;display:flex;flex-direction:column;overflow:hidden}.screen.enter{opacity:0}.screen.enter-active{opacity:1;transition:0.3s}.screen.exit{opacity:1}.screen.exit-active{opacity:0;transition:0.3s}@media (max-width: 1024px){.screen{padding-bottom:62px}}@media (max-width: 640px){.screen{padding-bottom:68px}}@media (max-width: 375px){.screen{padding-bottom:60px}}@media (max-width: 320px){.screen{padding-bottom:50px}}button{cursor:pointer;background-color:transparent}

